@import "../../App.scss";

.head-heading{
    font-size: 1.5em;
    font-weight: 600;

}
.company-info-section{
    background-color: white;
    margin: 2% 3% !important;
    border-radius: 10px;
    padding: 2%;
    h2{
        font-weight: 500;
        font-size: 1.5em;
    }
    .key-holders{
        font-weight: bold;
    }
    img{
        width: 100%;
        border-radius: 100%;
    }
}

.light-text{
    font-weight: 500;
}


@media (max-width: 992px) {

}
@media (max-width: 768px) {


}
@media (max-width: 576px) {
    .company-info-section{
        background-color: white;
        margin: 2% 3% !important;
        border-radius: 10px;
        padding: 2%;
        h2{
            font-weight: 500;
            font-size: 1.5em;
        }
        .key-holders{
            font-weight: bold;
            margin: 2% 0 !important;
        }
        img{
            width: 30%;
            display: block;
            margin: auto;
            border-radius: 100%;
        }
    }
}
